.contacts {
    padding: 40px 0;
    background-color: var(--dark-body-background-color);
    // background-image: url('../images/registration-bg-img/registration-bg-img@1x-mobile.jpeg');
}
.contacts__header {
    margin-bottom: 24px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    letter-spacing: 0.03em;
    color: var(--white-text-color);
    @include font(14px, 800, 18px);
}
.contacts__header-accent {
    color: var(--accent-color);
}
.contacts__form-header {
    margin-bottom: 40px;
    text-align: center;
    color: var(--white-text-color);
    letter-spacing: 0.08em;
    @include font(12px, 600, 15px);
}
.contacts__form-input {
    width: 100%;
    padding: 16px 28px;
    margin: 0;
    background-color: var(--dark-body-background-color);
    color: var(--body-background-color);
    align-items: center;
    justify-content: center;
    border: 1px solid var(--main-body-background-color);
    outline: none;
    resize: none;
    cursor: pointer;

    &:focus-within {
            outline: none;
            border: 1px solid var(--accent-color);
            color: var(--white-text-color);
        }

    @media screen and (min-width: 768px) {
        max-width: 336px;
    }
    @media screen and (min-width: 1366px) {
        max-width: 528px;
    }
}
::placeholder {
    color: var(--white-text-color);
    @include font (14px, 600, 17px);
}
.contacts__form-input:not(:last-child) {
    margin-bottom: 20px;
}
.contacts__form-button-wrapper {
    text-align: center;
}
.contacts__form-button {
    display: inline-block;
    margin-top: 40px;
    min-height: 48px;
    background-color: var(--accent-color);
    color: var(--white-text-color);
    letter-spacing: 0.08em;
    text-transform: uppercase;
    border: 1px solid var(--accent-color);
    @include font (14px, 700, 17px);
    cursor: pointer;

    @media screen and (min-width: 480px) {
        min-width: 280px;
    }
}
